import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './core/components/home/home.component';
import { LoginComponent } from './core/components/login/login.component';
import { MainComponent } from './core/components/main/main.component';
import { SearchAssistComponent } from './core/components/search-assist/search-assist.component';
import { TicketInsightsComponent } from './core/components/ticket-insights/ticket-insights.component';
import { EngineerSentimentsDataComponent } from './core/components/engineer-sentiments-data/engineer-sentiments-data.component';


const routes: Routes = [
  {path:'', redirectTo: 'login', pathMatch: 'full'},
  { path: 'login', component: LoginComponent },
  { path: 'main', component: MainComponent },
  { path: 'home', component: HomeComponent },
  { path: 'search-assist', component: SearchAssistComponent},
  { path: 'ticket-insights', component: TicketInsightsComponent},
  { path: 'engineer-sentiments-data', component: EngineerSentimentsDataComponent},
  

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
