//method to get the graph values
getCountOfCSATRatings(engineerSentiments){
  for(let i=0;i<engineerSentiments.length;i++){
    switch(engineerSentiments[i].csat ) { 
     case 1: { 
        this.oneRating++;
        break; 
      } 
     case 2: { 
        this.twoRating++;
        break; 
      } 
     case 3: { 
        this.threeRating++;
        break; 
      } 
     case 4: { 
        this.fourRating++;
        break; 
      } 
     case 5: { 
        this.fiveRating++;
        break; 
      } 
   
   } 
  }
}
//method to get the graph values
getCountOfFeedbackCategories(engineerSentiments){
  for(let i=0;i<this.totalEngineerSentiments;i++){
    if(engineerSentiments[i].closedButNotResolved)
        this.totalClosedButNotResolved++
      if(engineerSentiments[i].poorCommunication)
        this.totalPoorCommunication++
      if(engineerSentiments[i].poorQuality)
        this.totalPoorQuality++
      if(engineerSentiments[i].tooDifficult)
        this.totalDifficult++
      if(engineerSentiments[i].tooSlow)
        this.totalSlow++
  }
  let sortFeedbackCategories = new Map();
  sortFeedbackCategories.set("Not Resolved",this.totalClosedButNotResolved);
  sortFeedbackCategories.set("Poor Communication",this.totalPoorCommunication);
  sortFeedbackCategories.set("Poor Quality",this.totalPoorQuality);
  sortFeedbackCategories.set("Too Difficult",this.totalDifficult);
  sortFeedbackCategories.set("Too Slow",this.totalSlow);
  let sortedMap = new Map([...sortFeedbackCategories.entries()].sort((a,b) => b[1] - a[1]));
  this.labelArray = Array.from( sortedMap.keys());
  this.dataArray = Array.from( sortedMap.values());
  
}
//method to get the graph values
getCountOfSBGs(engineerSentiments){
  for(let i=0;i<this.totalEngineerSentiments;i++){
    switch(engineerSentiments[i].sbg ) { 
     case "CORP": { 
        this.corpSBG++;
        break; 
      } 
     case "AERO": { 
        this.aeroSBG++;
        break; 
      } 
     case "HBT": { 
        this.hbtSBG++;
        break; 
      } 
     case "HCE": { 
        this.hceSBG++;
        break; 
      } 
     case "PMT": { 
        this.pmtSBG++;
        break; 
      } 
      case "SPS": { 
        this.spsSBG++;
        break; 
      } 
      default: { 
        break; 
     } 

   
   } 
  }
}
//method to get the graph values
getRatingsArray(engineerSentiments){
  let date = new Date();
  date.setMonth(date.getMonth()-5);
  for(let i=0;i<6;i++){
    let monthYear = this.months[date.getMonth()]+' '+date.getFullYear();
    this.sixMonthCsatMap.set(monthYear, 0);
    this.sixMonthDsatMap.set(monthYear, 0);
    date.setMonth(date.getMonth()+1)
  }
  for (let i =0; i < engineerSentiments.length; i++){
    let sentiment = engineerSentiments[i];
    let createdDt = sentiment.createdDate;
    //let monthYear = this.datepipe.transform(createdDt, 'MMM yyyy')
    let monthYear = this.datepipe.transform(new Date(createdDt), 'MMM yyyy')
    if(sentiment.csat > 3){
      if(this.sixMonthCsatMap.get(monthYear) != undefined){
        let csatCount = this.sixMonthCsatMap.get(monthYear) + 1;
        this.sixMonthCsatMap.set(monthYear, csatCount);
      }      
    }else{
      if(this.sixMonthDsatMap.get(monthYear) != undefined){        
        let dsatCount = this.sixMonthDsatMap.get(monthYear) + 1;
        this.sixMonthDsatMap.set(monthYear, dsatCount);
      }
    }    
  }
}
