import { Component, OnInit, Output, EventEmitter  } from '@angular/core';
import { NgForm } from '@angular/forms';
import { CustomerModel, CustomerSentiments, CustomerSentimentModel, CustomerSentimentData } from '../../models/customer-model';
import { RestService } from '../../services/rest.service';
import { TicketInsightModel } from '../../models/ticket-insight-model';
import { Router } from '@angular/router';
import { SessionStorageService } from 'ngx-webstorage';
import { TicketInsightRequestModel, CINamesModel } from '../../models/mailcontent-model';
import { SelectItem } from 'primeng/api/selectitem';
import { LoggingModel } from 'src/app/logging-model';


declare function showModal(id): any;
@Component({
  selector: 'app-search-assist',
  templateUrl: './search-assist.component.html',
  styleUrls: ['./search-assist.component.css']
})

export class SearchAssistComponent implements OnInit {
  textVal: string;
  appName: string;
  shortDescription: string;
  problemDescription: string;
  mailContent: string;
  priority: string;
  customerModel: any = {};
  customerData: CustomerModel = new CustomerModel();
  customerSentimentData: CustomerSentimentModel = new CustomerSentimentModel();
  ticketInsightData: TicketInsightModel = new TicketInsightModel();
  customerSentiments : CustomerSentimentData[];
  customerSentimentsDump : CustomerSentiments[];
  ticketInsightRequestData: TicketInsightRequestModel = new TicketInsightRequestModel();
  columns: any[] = new Array();
  showInsights: boolean = true;
  disableAppName: boolean =true;
  disableDescription: boolean =true;
  tmpProgressBar: boolean = true;
  disableProblemDescription: boolean = true;
  username: string;
  isShow = true;
  ciNamesArray: CINamesModel = new CINamesModel();
  configurationItems:SelectItem[];
  item: string;
  display='none';
  assistMeClicked: boolean = false;
  sentimentsDataLoaded: boolean = false;
  insightsDataLoaded: boolean = false;
  loggingEvent: LoggingModel = new LoggingModel();
  componentName = "Search-Assist-Component";

  constructor(private restApiService: RestService, private router: Router, private sessionStorage: SessionStorageService) { }

  async ngOnInit() {
    this.username = this.sessionStorage.retrieve("username");
    this.ciNamesArray = <CINamesModel>await this.restApiService
    .getCINames()
    .toPromise();
    this.configurationItems = [];
    for (let i = 0; i < this.ciNamesArray.ciNames.length; i++) {
      this.configurationItems.push({label: this.ciNamesArray.ciNames[i], value: this.ciNamesArray.ciNames[i]});
    }
  }

  //mail pasted and data loaded in the page
  customerDetails(customerForm: NgForm) {
    
    let mailContent = customerForm.controls['mailContent'].value;
    if(mailContent === undefined || mailContent === ''){
      this.isShow = true;
      return;
    }
    this.isShow = false;
    this.textVal = mailContent;
    customerForm.controls['mailContent'].setValue("");
    this.restApiService.getCustomerDetails(mailContent).subscribe((response: CustomerModel) => {
      this.customerData = response;
      this.appName = this.customerData.appName;
      this.shortDescription = this.customerData.shortDescription;
      this.problemDescription = this.customerData.problemDescription;
      this.priority = this.customerData.priority;

      this.disableProblemDescription = true;
      this.disableAppName = true;
      this.disableDescription = true;
      this.getCustomerSentimentsDetails(this.customerData.name);
    });
    this.showInsights = false;
  }

  getCustomerSentimentsDetails(name: string) {
    this.restApiService.getCustomerSentimentDetails(name).subscribe((response: CustomerSentimentModel) => {
      this.customerSentimentData=response;
      this.customerSentiments = this.customerSentimentData.customersentiments;
      this.sessionStorage.store("customerSentiments", this.customerSentiments);
    });
    this.restApiService.getCustomerSentimentDetailsDump(name).subscribe((response: CustomerSentiments[]) => {
      this.customerSentimentsDump = response;
     // this.customerSentimentsDump = [{"incident":"INC0002845561","name":"Adam Epps","createdDate":"2020-09-20","csat":5,"comments":"Very quick on the response and fix..great people and process!","closedButNotResolved":false,"tooSlow":false,"tooDifficult":false,"poorCommunication":false,"poorQuality":false,"feedbackRCA":"","locationid":"US"},{"incident":"INC0002736179","name":"Adam Epps","createdDate":"2020-10-31","csat":5,"comments":"Great and fast service!","closedButNotResolved":false,"tooSlow":false,"tooDifficult":true,"poorCommunication":false,"poorQuality":false,"feedbackRCA":"","locationid":"EMEA"},{"incident":"INC0002762436","name":"Adam Epps","createdDate":"2020-07-20","csat":2,"comments":"The communication that my problem was resolved cited someone else's problem and didn't correctly describe what was happening. The problem was resolved, but i had to try it to see. I was told that my Compass req was circulated to all the COEs because there wasn't a buyer listed for my req. It was catalog requisition. The people sending me emails that my req was coming to them for approval were business leaders from around the world, not procurement COEs. I would rather you tell me: ","closedButNotResolved":false,"tooSlow":true,"tooDifficult":false,"poorCommunication":true,"poorQuality":true,"feedbackRCA":"Communication gap\/Ticket not updated","locationid":"US"}]
      this.sentimentsDataLoaded = true;
      this.sessionStorage.store("customerSentimentsDump", this.customerSentimentsDump);
      if(this.assistMeClicked && this.customerSentiments!=undefined) {
        this.router.navigate(['/ticket-insights']);
      }
    });   
  }

  //collapsible uncollapsible icon funtionality
  toggleShow() {
    this.isShow = !this.isShow;
  }

  //assist me click functionality
  insightDetails(ticketDataForm: NgForm) {
    this.assistMeClicked = true; //true, if assistMe button clicked
    let callerName = this.customerData.name;
    let appName = ticketDataForm.controls['appName'].value;
    let shortDescription = ticketDataForm.controls['shortDescription'].value;
    let problemDescription = ticketDataForm.controls['problemDescription'].value;

    this.ticketInsightRequestData.appName = appName;
    this.ticketInsightRequestData.callerName = callerName;
    this.ticketInsightRequestData.shortDescription = shortDescription;
    this.ticketInsightRequestData.problemDescription = problemDescription;

    this.sessionStorage.store("callerName", callerName);
    this.sessionStorage.store("appName", appName);
    this.sessionStorage.store("shortDescription", shortDescription);
    this.sessionStorage.store("problemDescription", problemDescription);
    this.sessionStorage.store("priority", this.customerData.priority);
    this.sessionStorage.store("mailContent", this.textVal);
    this.sessionStorage.store("username", this.username);
    this.sessionStorage.store("locationId", this.customerData.location);

    //check if sentiment details has already loaded
    if(this.customerSentimentData.customersentiments != undefined && this.customerSentimentsDump!=undefined) {
      this.router.navigate(['/ticket-insights']);
    } else {
      this.router.navigate(['/ticket-insights']);
      this.restApiService.getInsightDetails(this.ticketInsightRequestData).subscribe((response: TicketInsightModel) => {
        this.ticketInsightData = response;
        this.sessionStorage.store("ticketInsightData", this.ticketInsightData);
        this.insightsDataLoaded = true;
        this.router.navigate(['/ticket-insights']);
      });
    }

    if((this.sentimentsDataLoaded == false) && (this.insightsDataLoaded == false)) {
      console.log("Data still loading!");
    }
  }

  makeEditableAppName() {
    this.disableAppName = false;
  }

  makeEditableDescription() {
    this.disableDescription = false;
    //event logging
    let loggingEventData = { ...this.loggingEvent};
    loggingEventData.componentName = this.componentName;
    loggingEventData.eventTriggered = "Edit Description Clicked"
    this.restApiService.reportLoggingEvent(loggingEventData).subscribe((response: boolean)=>{});
  }

  makeEditableProblemDescription() {
    this.disableProblemDescription = false;
    //event logging
    let loggingEventData = { ...this.loggingEvent};
    loggingEventData.componentName = this.componentName;
    loggingEventData.eventTriggered = "Edit Problem Description Clicked"
    this.restApiService.reportLoggingEvent(loggingEventData).subscribe((response: boolean)=>{});
  }

  //after selecting dropdown value of ci
  async getDropdownValue(ciSelected) {
    console.log(ciSelected);
    this.disableAppName = false;
    //event logging
    let loggingEventData = { ...this.loggingEvent};
    loggingEventData.componentName = this.componentName;
    loggingEventData.eventTriggered = "App Name Dropdown value changed"
    this.restApiService.reportLoggingEvent(loggingEventData).subscribe((response: boolean)=>{});
  }
    
  close() {
    this.display = 'none';
  }
}
