import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpResponse, HttpErrorResponse, HttpHeaders} from '@angular/common/http'
import { environment } from 'src/environments/environment';
import { AppConstants } from '../constants/app-constants';
import { DashboardData, SupportEngineerSentiments } from '../models/support-engineer-sentiments';
import { UserAuthenticationModel, AuthenticationResults } from '../models/authentication-results';
import { MailcontentModel, TicketInsightRequestModel, CINamesModel } from '../models/mailcontent-model';
import { CustomerModel, CustomerSentimentData, CustomerSentimentModel, CustomerSentiments } from '../models/customer-model';
import { TicketInsightModel } from '../models/ticket-insight-model';
import { RecommendationDetails } from '../models/recommendation-details';
import { ResolutionFeedbackModel, AionFeedbackModel, FeatureSuggestionsModel, ContentSuggestionModel, TrainingModel } from '../models/resolution-feedback-model';
import { LoggingModel } from 'src/app/logging-model';

@Injectable({
  providedIn: 'root'
})
export class RestService {
  apiBaseURL: string = environment.apiBaseURL;

  constructor(private httpClient: HttpClient) { }
  getAuthenticationResults(userAuthenticationModel: UserAuthenticationModel){
    
    let authenticateUserJson = JSON.stringify(userAuthenticationModel);
    const httpOptions = {
      
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    }
    return this.httpClient.post<AuthenticationResults>(`${this.apiBaseURL}${AppConstants.SUPPORTFOCAL_ENDPOINT}`
   ,authenticateUserJson, httpOptions)
  
  }
  getSelectedEngineerDetails(engineerSelected){
    
    //let authenticateUserJson = JSON.stringify(userAuthenticationModel);
    const httpOptions = {
      params: new HttpParams().set('supportengineer',engineerSelected),
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    }    
    // return this.httpClient.get<SupportEngineerSentiments[]>(`${this.apiBaseURL}${AppConstants.ENGINEER_FOCAL_ENDPOINT}`
    //      , httpOptions);
    return this.httpClient.get<DashboardData>(`${this.apiBaseURL}${AppConstants.ENGINEER_FOCAL_ENDPOINT}`
         , httpOptions);  
  }
  getEngineerDetails(engineerSelected){
    
    //let authenticateUserJson = JSON.stringify(userAuthenticationModel);
    const httpOptions = {
      params: new HttpParams().set('supportengineer',engineerSelected),
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    }    
    // return this.httpClient.get<SupportEngineerSentiments[]>(`${this.apiBaseURL}${AppConstants.ENGINEER_FOCAL_ENDPOINT}`
    //      , httpOptions);
    return this.httpClient.get<any[]>(`${this.apiBaseURL}${AppConstants.ENGINEER_FOCAL_DATADUMP_ENDPOINT}`
         , httpOptions);  
  }
  getSupportEngineerSentimentAnalysis(){
    
    const httpOptions = {
      
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    }
    return this.httpClient.post<SupportEngineerSentiments[]>(`${this.apiBaseURL}${AppConstants.SUPPORTFOCAL_ENDPOINT}`
   , httpOptions)
  }

  getCustomerDetails(mailContent : MailcontentModel){
    let mailContentJson = JSON.stringify(mailContent);
    const httpOptions = {
      
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    }
    return this.httpClient.post<CustomerModel>(`${this.apiBaseURL}${AppConstants.CUSTOMER_DETAIL_ENDPOINT}`
   , mailContentJson, httpOptions)
  }

  getCustomerSentimentDetails(name : String){
    let mailContentJson = JSON.stringify(name);
    const httpOptions = {
      
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    }
    return this.httpClient.post<CustomerSentimentModel>(`${this.apiBaseURL}${AppConstants.CUSTOMER_SENTIMENT_DETAIL_ENDPOINT}`
   , mailContentJson, httpOptions)
  }

  getCustomerSentimentDetailsDump(name : String){
    let mailContentJson = JSON.stringify(name);
    const httpOptions = {
      
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    }
    return this.httpClient.post<CustomerSentiments[]>(`${this.apiBaseURL}${AppConstants.CUSTOMER_SENTIMENT_DETAIL_DUMP_ENDPOINT}`
   , mailContentJson, httpOptions)
  }

  reportLoggingEvent(logEvent: LoggingModel) {
    let logEventJson = JSON.stringify(logEvent);
    console.log("logEventJson : " +logEventJson);
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    }
    return this.httpClient.post<boolean>(`${this.apiBaseURL}${AppConstants.LOGGING_EVENT}`
    , logEventJson, httpOptions)
  }

  getInsightDetails(ticketInsightRequestData: TicketInsightRequestModel){
    let ticketInsightRequestDataJson = JSON.stringify(ticketInsightRequestData);
    const httpOptions = {
      // params: new HttpParams().set('callerName',callerName).append('appName',appName).append('shortDescription',shortDescription).append(),
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    }    
    return this.httpClient.post<TicketInsightModel>(`${this.apiBaseURL}${AppConstants.TICKETS_INSIGHTS_ENDPOINT}`
         , ticketInsightRequestDataJson, httpOptions);
  }
  getRecommendationDetails(title){
    const httpOptions = {
      params: new HttpParams().set('title',title),
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    }    
    return this.httpClient.get<RecommendationDetails>(`${this.apiBaseURL}${AppConstants.RECOMMENDATION_DETAILS_ENDPOINT}`
         , httpOptions);
  }
  submitUserRecommendationFeedback(resolutionFeedback : ResolutionFeedbackModel){
    let resolutionFeedbackJson = JSON.stringify(resolutionFeedback);
    const httpOptions = {
      
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    }
    return this.httpClient.post<boolean>(`${this.apiBaseURL}${AppConstants.RECOMMENDATION_FEEDBACK_FOCAL_ENDPOINT}`
   , resolutionFeedbackJson, httpOptions)
  } 

  submitAionUserFeedback(aionFeedback : AionFeedbackModel){
    let aionUserFeedbackJson = JSON.stringify(aionFeedback);
    const httpOptions = {
      
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    }
    return this.httpClient.post<boolean>(`${this.apiBaseURL}${AppConstants.AION_USER_FEEDBACK_FOCAL_ENDPOINT}`
   , aionUserFeedbackJson, httpOptions)
  } 

  submitSuggestions(aionFeedback : FeatureSuggestionsModel){
    let aionUserFeedbackJson = JSON.stringify(aionFeedback);
    const httpOptions = {
      
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    }
    return this.httpClient.post<boolean>(`${this.apiBaseURL}${AppConstants.AION_USER_SUGGESTIONS_ENDPOINT}`
   , aionUserFeedbackJson, httpOptions)
  } 

  submitContent(aionFeedback : ContentSuggestionModel){
    let aionUserFeedbackJson = JSON.stringify(aionFeedback);
    const httpOptions = {
      
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    }
    return this.httpClient.post<boolean>(`${this.apiBaseURL}${AppConstants.AION_USER_CONTENT_ENDPOINT}`
   , aionUserFeedbackJson, httpOptions)
  } 

  submitTrainingDocument(aionFeedback : TrainingModel){
    let aionUserFeedbackJson = JSON.stringify(aionFeedback);
    const httpOptions = {
      
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    }
    return this.httpClient.post<boolean>(`${this.apiBaseURL}${AppConstants.AION_USER_TRAINING_ENDPOINT}`
   , aionUserFeedbackJson, httpOptions)
  } 

  getCINames(){
    
    const httpOptions = {
      
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    }
    return this.httpClient.get<CINamesModel>(`${this.apiBaseURL}${AppConstants.CI_NAMES}`
    , httpOptions);
  } 

  logout(){
    
    const httpOptions = {
      
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    }
    return this.httpClient.get<boolean>(`${this.apiBaseURL}${AppConstants.LOGOUT}`
    , httpOptions);
  }

  
}
