import { Component, OnInit } from '@angular/core';
import { SelectItem } from 'primeng/api/selectitem';
import { TrainingModel } from '../../models/resolution-feedback-model';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { RestService } from '../../services/rest.service';
declare function showModal(id): any;
@Component({
  selector: 'app-training-aion',
  templateUrl: './training-aion.component.html',
  styleUrls: ['./training-aion.component.css']
})
export class TrainingAionComponent implements OnInit {

  trainingType: SelectItem[] = [];
  item: string;
  trainingSuggestion: TrainingModel = new TrainingModel();
  suggestTrainingForm: FormGroup;
  feedbackResponse: boolean;
  submittedTraining=false;

  constructor(private restApiService: RestService, private formBuilder: FormBuilder) {}

  ngOnInit(): void {
    this.trainingType.push({label: "Process", value:"Process"});
    this.trainingType.push({label: "Domain", value:"Domain"});
    this.trainingType.push({label: "Technical", value:"Technical"});
    this.suggestTrainingForm = this.formBuilder.group({
      domain:['',Validators.required],
      trainingType:['', Validators.required],
      trainingTitle:['', Validators.required],
      trainingDescription:['', Validators.required]
    })
  }

  submitTraining(){
    this.submittedTraining=true;
    if(this.suggestTrainingForm.invalid){
      return;
    }
    let domain = this.suggestTrainingForm.controls['domain'].value;
    let trainingType = this.suggestTrainingForm.controls['trainingType'].value;
    let trainingTitle = this.suggestTrainingForm.controls['trainingTitle'].value;
    let trainingDescription = this.suggestTrainingForm.controls['trainingDescription'].value;
    
    this.trainingSuggestion.domain = domain;
    this.trainingSuggestion.trainingType = trainingType;
    this.trainingSuggestion.trainingTitle = trainingTitle;
    this.trainingSuggestion.trainingResolution = trainingDescription;
    this.restApiService.submitTrainingDocument(this.trainingSuggestion).subscribe((response: boolean) => {
      this.feedbackResponse = response;
      if(this.feedbackResponse){
        showModal('success-Modal');
      }
    });
   this.suggestTrainingForm.reset();
   this.submittedTraining=false;
  }

}
