import { Component, OnInit } from '@angular/core';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { RestService } from '../../services/rest.service';
import { ContentSuggestionModel, FeatureSuggestionsModel } from '../../models/resolution-feedback-model';
declare function showModal(id): any;
@Component({
  selector: 'app-suggest-content',
  templateUrl: './suggest-content.component.html',
  styleUrls: ['./suggest-content.component.css']
})
export class SuggestContentComponent implements OnInit {
  suggestionForm: FormGroup;
  contentSuggestion: ContentSuggestionModel = new ContentSuggestionModel();
  featureSuggestion: FeatureSuggestionsModel = new FeatureSuggestionsModel();
  feedbackResponse:boolean;
  suggestContentForm: FormGroup;
  submittedContent=false;
  submittedSuggestion=false;
  constructor(private restApiService: RestService,private formBuilder: FormBuilder) { }

  ngOnInit(): void {
    this.suggestionForm = this.formBuilder.group({
      title:['',Validators.required],
      description:['', Validators.required]
    })
    this.suggestContentForm = this.formBuilder.group({
      title:['',Validators.required],
      resolution:['', Validators.required]
    })
  }

  submitSuggestion(){
    this.submittedSuggestion=true;
    if(this.suggestionForm.invalid){
      return;
    }
    let title = this.suggestionForm.controls['title'].value;
    let description = this.suggestionForm.controls['description'].value;

    this.featureSuggestion.title = title;
    this.featureSuggestion.description = description;
    
    this.restApiService.submitSuggestions(this.featureSuggestion).subscribe((response: boolean) => {
      this.feedbackResponse = response;
      if(this.feedbackResponse){
        showModal('success-Modal');
      }
    });
    this.suggestionForm.reset();
    this.submittedSuggestion=false;
  }

  submitContent(){
    this.submittedContent=true;
    if(this.suggestContentForm.invalid){
      return;
    }
    let title = this.suggestContentForm.controls['title'].value;
    let resolution = this.suggestContentForm.controls['resolution'].value;
    
    this.contentSuggestion.title = title;
    this.contentSuggestion.resolution = resolution;
    this.restApiService.submitContent(this.contentSuggestion).subscribe((response: boolean) => {
      this.feedbackResponse = response;
      if(this.feedbackResponse){
        showModal('success-Modal');
      }
    });
    this.suggestContentForm.reset();
    this.submittedContent=false;
  }

}
