import { Component, OnInit } from '@angular/core';
import { SessionStorageService } from 'ngx-webstorage';
import { Router } from '@angular/router';
import { DashboardData, SupportEngineerSentiments } from '../../models/support-engineer-sentiments';
import { RestService } from '../../services/rest.service';
import {FormBuilder, FormGroup, Validators } from '@angular/forms'
import { AionFeedbackModel } from '../../models/resolution-feedback-model';
declare function closeModal(id): any;
declare function showModal(id): any;
@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainComponent implements OnInit {
  //myDashboardData: SupportEngineerSentiments[];
  myDashboardData: DashboardData;
 
  mySessionDashboardData: SupportEngineerSentiments[];
  username: string;
  feedbackForm: FormGroup;
  aionFeedback: AionFeedbackModel = new AionFeedbackModel();
  feedbackResponse : boolean;
  submittedFeedback=false;
  myDashboardDumpData:SupportEngineerSentiments[];

  constructor(private restApiService: RestService,private router: Router,
    private sessionStorage: SessionStorageService, private sessionStorage1: SessionStorageService,
    private formBuilder: FormBuilder) { }

  isHome = false;
  isSearchAssist = true;
  isEnggSentiments = false;
  isSuggestContent = false;
  isTrainingAion = false;
  searchAssistActive = true;
  homeActive = false;
  loading = false;
  ngOnInit(): void {
    this.username = this.sessionStorage.retrieve("username");
    this.validateEntry();
    this.feedbackForm = this.formBuilder.group({
      resolutionKnown:[''],
      caseType:[''],
      title:['',Validators.required],
      resolution:['', Validators.required],
      other:['']
    })
  }

  setActiveClass(navContentName){
    if(navContentName === 'home'){
      this.homeActive = true;
      this.searchAssistActive = false;
    }else if(navContentName === 'home'){
      this.homeActive = false;
      this.searchAssistActive = true;
    }
  }

  async routeTo(navContentName: string){
    this.mySessionDashboardData = this.sessionStorage.retrieve("myDashboardData");
    //this.mySessionDashboardData = this.sessionStorage.retrieve("myDashboardDataDump");
    // this.setActiveClass(navContentName);
    if(navContentName == 'home'){
      this.resetFlags();      
      
      this.loading = true;
      if(this.mySessionDashboardData==null || this.mySessionDashboardData==undefined){
        this.myDashboardDumpData = <any[]>await this.restApiService
         .getEngineerDetails(this.username)
         .toPromise();
         console.log('myDashboardDataDump :: '+this.myDashboardDumpData)
         
        if(!this.myDashboardDumpData){
          showModal('noData-Modal');
          this.resetFlags();
          this.isSearchAssist = true;
          this.loading = false;
        }else{
          this.myDashboardData = <DashboardData>await this.restApiService
         .getSelectedEngineerDetails(this.username)
         .toPromise();
         console.log('myDashboardData :: '+this.myDashboardData)
         
        //    this.myDashboardDataDump = 
        //  [
        //    {"name":"Sonal","incident":"INC0002845561","createdDate":"2020-09-20","csat":5,"comments":"Very quick on the response and fix..great people and process!","closedButNotResolved":false,"tooSlow":false,"tooDifficult":false,"poorCommunication":false,"poorQuality":false,"feedbackRCA":"","domain":"HPS","sbg":"PMT","region":"US"},
        //    {"name":"Sonal","incident":"INC0002681750","createdDate":"2020-09-30","csat":2,"comments":"Issue is not resolved yet!","closedButNotResolved":true,"tooSlow":true,"tooDifficult":true,"poorCommunication":true,"poorQuality":true,"feedbackRCA":"Cannot Define","domain":"MSC","sbg":"AERO","region":"US"},{"name":"Sonal","incident":"INC0002736179","createdDate":"2020-10-31","csat":5,"comments":"Great and fast service!","closedButNotResolved":false,"tooSlow":false,"tooDifficult":true,"poorCommunication":false,"poorQuality":false,"feedbackRCA":"","domain":"ISCG","sbg":"CORP","region":"EMEA"},
        //    {"name":"Sonal","incident":"INC0002762436","createdDate":"2020-07-20","csat":2,"comments":"The communication that my problem was resolved cited someone else's problem and didn't correctly describe what was happening. The problem was resolved, but i had to try it to see. I was told that my Compass req was circulated to all the COEs because there wasn't a buyer listed for my req. It was catalog requisition. The people sending me emails that my req was coming to them for approval were business leaders from around the world, not procurement COEs. I would rather you tell me: ","closedButNotResolved":false,"tooSlow":true,"tooDifficult":false,"poorCommunication":true,"poorQuality":true,"feedbackRCA":"Communication gap\/Ticket not updated","domain":"UOP","sbg":"PMT","region":"US"}
        //  ]
        this.sessionStorage.store("myDashboardData",this.myDashboardData);
        this.sessionStorage.store("myDashboardDataDump",this.myDashboardDumpData);
        this.isHome = true;
        this.loading = false;
        }
        
      }
     
      
    }
    if(navContentName == 'search-assist'){
      this.resetFlags();
      this.isSearchAssist = true;
    }
    if(navContentName == 'engineer-sentiments-data'){
      this.resetFlags();
      this.isEnggSentiments = true;
    }
    if(navContentName == 'suggest-content'){
      this.resetFlags();
      this.isSuggestContent = true;
    }
    if(navContentName == 'training-aion'){
      this.resetFlags();
      this.isTrainingAion = true;
    }
  }

  resetFlags(){
    this.isHome=false;
    this.isSearchAssist=false;
    this.isEnggSentiments=false;
    this.isSuggestContent = false;
    this.isTrainingAion = false;
  }


  validateEntry(){
    if(this.username=="" ||this.username==null || this.username==undefined){
      this.sessionStorage.clear();
      this.router.navigate(['/login']);
    }
  }

  submitFeedback(){
    this.submittedFeedback=true;
    if(this.feedbackForm.invalid){
      return;
    }
    let title = this.feedbackForm.controls['title'].value;
    let resolution = this.feedbackForm.controls['resolution'].value;
    let other = this.feedbackForm.controls['other'].value;
    let resolutionKnown = this.feedbackForm.controls['resolutionKnown'].value;
    let caseType = this.feedbackForm.controls['caseType'].value;
    if(resolutionKnown == ""){
      resolutionKnown = false;
    }
    if(caseType == ""){
      caseType = false;
    }
    this.aionFeedback.knowResolution = resolutionKnown;
    this.aionFeedback.enrichMe = caseType;
    this.aionFeedback.title = title;
    this.aionFeedback.resolution = resolution;
    this.aionFeedback.other = other;
    this.restApiService.submitAionUserFeedback(this.aionFeedback).subscribe((response: boolean) => {
      this.feedbackResponse = response;
      if(this.feedbackResponse){
        this.sessionStorage.store("submittedFeedback",this.submittedFeedback);
        showModal('success-Modal');

      }
    });
    
    closeModal('feedback-Modal');

  }
}
