import { Component, OnInit } from '@angular/core';
import { SessionStorageService } from 'ngx-webstorage';
import { AuthenticationResults } from '../../models/authentication-results';
import { SupportEngineerSentiments } from '../../models/support-engineer-sentiments';

@Component({
  selector: 'app-engineer-sentiments-data',
  templateUrl: './engineer-sentiments-data.component.html',
  styleUrls: ['./engineer-sentiments-data.component.css']
})
export class EngineerSentimentsDataComponent implements OnInit {
  authenticationResults: AuthenticationResults = new AuthenticationResults();
  engineerSentiments: SupportEngineerSentiments[];
  totalFeeback: number;
  loading = true;
  username : string;

  constructor(private sessionStorage: SessionStorageService) { }

  ngOnInit(): void {
    this.engineerSentiments = this.sessionStorage.retrieve("myDashboardData");
    this.username = this.sessionStorage.retrieve("username");
    //this.engineerSentiments = this.authenticationResults.engineerSentiments;
    this.totalFeeback = this.engineerSentiments.length;
    this.loading = false;
    
  }

}
