import { Component } from '@angular/core';
import { NavigationStart, Router } from '@angular/router';
import {Idle, DEFAULT_INTERRUPTSOURCES} from '@ng-idle/core';
import { SessionStorageService } from 'ngx-webstorage';
import { RestService } from './core/services/rest.service';
import { LoggingModel } from './logging-model';

declare function showModal(id): any;
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
 
})
export class AppComponent {
  title = 'aion';
  showHead: boolean = false;
  loggingEvent: LoggingModel = new LoggingModel();
  componentName = "App-Component";
  idleState = 'Not started.';
  timedOut = false;
  submittedFeedback: boolean;
  ngOnInit() {
  }

  constructor(private restApiService: RestService, private router: Router, private idle: Idle, private sessionStorage: SessionStorageService) {
  // on route change to '/login', set the variable showHead to false
  this.submittedFeedback = this.sessionStorage.retrieve("submittedFeedback");
  idle.setIdle(120);
    // sets a timeout period of 5 seconds. after 10 seconds of inactivity, the user will be considered timed out.
  idle.setTimeout(120);
  // sets the default interrupts, in this case, things like clicks, scrolls, touches to the document
  idle.setInterrupts(DEFAULT_INTERRUPTSOURCES);

  idle.onIdleEnd.subscribe(() => this.idleState = 'No longer idle.');
  idle.onIdleStart.subscribe(() => {
      this.idleState = 'You\'ve gone idle!'
      this.submittedFeedback = this.sessionStorage.retrieve("submittedFeedback");
      if(!this.submittedFeedback){
        showModal('feedback-Modal');
        this.reset();
        //event logging
        let loggingEventData = { ...this.loggingEvent};
        loggingEventData.componentName = this.componentName;
        loggingEventData.eventTriggered = "aiON automatic feedback form popped up."
        this.restApiService.reportLoggingEvent(loggingEventData).subscribe((response: boolean)=>{});
      }
  });
  this.reset();

    router.events.forEach((event) => {
      if (event instanceof NavigationStart) {
        if (event['url'] == '/login' || event['url'] == '/') {
          this.showHead = false;
        } else {
          this.showHead = true;
        }
      }
    });
  }

  reset() {
    this.idle.watch();
    this.idleState = 'Started.';
    this.timedOut = false;
  }

}
