import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { RestService } from '../../services/rest.service';
import { UserAuthenticationModel, AuthenticationResults } from '../../models/authentication-results';
import { SessionStorageService } from 'ngx-webstorage';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  loginForm: FormGroup;
  loading = false;
  usernameRequired = false;
  passwordRequired = false;
  submitted: boolean;
  returnUrl: string;
  userAuthenticationModel: UserAuthenticationModel = new UserAuthenticationModel();
  authenticationResults: AuthenticationResults = new AuthenticationResults();
  
    
  constructor(private router: Router,  private formBuilder: FormBuilder, private restApiService: RestService, private sessionStorage: SessionStorageService) { }

  ngOnInit(): void {
    this.submitted = false;
    this.sessionStorage.clear();
    this.loginForm = this.formBuilder.group({
      username: ['', Validators.required],
      password: ['', Validators.required]
  });
  }
  get f() { return this.loginForm.controls; }
  async onSubmit() {
    this.usernameRequired = false;
    this.passwordRequired = false;
    if(this.f.username.value==""&& this.f.password.value==""){
      this.usernameRequired=true;
      this.passwordRequired=true;
      return;
    }
    if(this.f.username.value==""){
      this.usernameRequired=true;
      return;
    }
    if(this.f.password.value==""){
      this.passwordRequired=true;
      return;
    }
    this.loading = true;
    this.f['username'].disable();
    this.f['password'].disable();
    this.userAuthenticationModel.username = this.f.username.value;
    this.userAuthenticationModel.password = this.f.password.value;
    this.authenticationResults = <AuthenticationResults>await this.restApiService
      .getAuthenticationResults(this.userAuthenticationModel)
      .toPromise();
    console.log(this.authenticationResults.responseMessage);
      
    // });
    if(this.authenticationResults.responseMessage=="error"){
      this.sessionStorage.clear();
      this.router.navigate(['/login']);
      this.submitted = true;
      this.loading = false;
      this.f['username'].enable();
      this.f['password'].enable();
      
    }else if(this.authenticationResults.responseMessage=="data"){
      this.sessionStorage.store("username",this.f.username.value);
      this.router.navigate(['/main']);
    }
    
  }
}