import { Component, OnInit, Input } from '@angular/core';
import { SessionStorageService} from 'ngx-webstorage';
import { TicketInsightModel, ResolutionRecommendations} from '../../models/ticket-insight-model';
import { RestService } from '../../services/rest.service';
import { Router } from '@angular/router';
import { ResolutionFeedbackModel } from '../../models/resolution-feedback-model';
import { CustomerSentimentData, CustomerSentiments } from '../../models/customer-model';
import { TicketInsightRequestModel } from '../../models/mailcontent-model';
import { LoggingModel } from 'src/app/logging-model';

declare function closeModal(id): any;
declare function showModal(id): any;

@Component({
  selector: 'app-ticket-insights',
  templateUrl: './ticket-insights.component.html',
  styleUrls: ['./ticket-insights.component.css']
})

export class TicketInsightsComponent implements OnInit {
  callerName: string;
  appName: string;
  shortDescription: string;
  problemDescription: string;
  mailContent: string;
  priority: string;
  ticketInsightData: TicketInsightModel = new TicketInsightModel();
  customerSentimentsDump : CustomerSentiments[];
  customerSentiment: CustomerSentimentData;
  recommendations: ResolutionRecommendations[];
  recommendationDetails: ResolutionRecommendations = new ResolutionRecommendations();
  resolutionFeedback : ResolutionFeedbackModel = new ResolutionFeedbackModel();
  ticketInsightRequestData: TicketInsightRequestModel = new TicketInsightRequestModel();
  feedbackResponse : boolean;
  totalFeedback: number ;
  totalCSATs: number = 0;
  totalDSATs:number = 0;
  totalClosedButNotResolved:number = 0;
  totalPoorCommunication:number = 0;
  totalPoorQuality:number = 0;
  totalDifficult:number = 0;
  totalSlow:number = 0;
  avgRating:number = 0;
  totalRating:number = 0;
  public canvasWidth: number;
  public needleValue: number;
  public centralLabel: string;
  public options;
  public bottomLabel;
  pieData: any;
  loading = true;
  cols = [];
  horizontalBarGraphChartOptions: any;
  horizontalBarGraphData: any;
  labelArray = [];
  dataArray = [];
  userFeedback: string;
  showAvgRatingGraph : boolean = false;
  showTotalFeedbackGraph : boolean = false;
  showFeedbackCategoriesGraph : boolean = false;
  showAvgAlert : boolean=false;
  pieGraphChartOptions:any
  textMessage : string;  
  msgHideAndShow : boolean; 
  loginUserName : string;
  feedbackMap = new Map();
  resFeedback : ResolutionFeedbackModel;
  showNoData =  false;
  showNoGraph = false;
  locationId: string;
  retrievedCustomerSentiment: boolean = false;
  observedCustomerSentiment: boolean = false;
  sentimentsDataLoadedInInsight: boolean = false;
  ticketInsightLoaded: boolean = false;
  categoryArray = new Array(); categoryCountArray = new Array();
  componentName = "Ticket-Insight-Component";
  loggingEvent: LoggingModel = new LoggingModel();
  showGaugeChart: boolean = false;

  constructor(private sessionStorage: SessionStorageService, private restApiService: RestService, private router: Router) { }

  ngOnInit(): void {
    let resRecommendations = new ResolutionRecommendations();
    this.resFeedback = new ResolutionFeedbackModel();
    this.resFeedback.recommendationDetails = resRecommendations;

    //Fetching the session variables
    this.loginUserName = this.sessionStorage.retrieve("username");
    this.validateEntry();
    this.callerName = this.sessionStorage.retrieve("callerName");
    this.appName = this.sessionStorage.retrieve("appName");
    this.shortDescription = this.sessionStorage.retrieve("shortDescription");
    this.problemDescription = this.sessionStorage.retrieve("problemDescription");
    this.priority = this.sessionStorage.retrieve("priority");
    this.mailContent = this.sessionStorage.retrieve("mailContent");
    
    //check if the ticketInsightData is being retrieved from session storage
    if(this.sessionStorage.retrieve("ticketInsightData") == null) {
      this.ticketInsightRequestData.appName = this.appName;
      this.ticketInsightRequestData.callerName = this.callerName;
      this.ticketInsightRequestData.shortDescription = this.shortDescription;
      this.ticketInsightRequestData.problemDescription = this.problemDescription;
      this.restApiService.getInsightDetails(this.ticketInsightRequestData).subscribe((response: TicketInsightModel) => {
        this.ticketInsightData = response;
        this.loadRecommendation();
      });
    } else {
      this.ticketInsightData = this.sessionStorage.retrieve("ticketInsightData");
      this.loadRecommendation();
    }
    this.customerSentiment = this.sessionStorage.retrieve("customersentiments");
    this.customerSentimentsDump = this.sessionStorage.retrieve("customerSentimentsDump");
    //check if the customer sentiments is being retrieved from session storage
    if(this.customerSentimentsDump!=null && this.customerSentiment!=null) {
      this.sentimentsDataLoadedInInsight = true;
      this.customerSentiment = this.sessionStorage.retrieve("customersentiments");
      this.customerSentimentsDump = this.sessionStorage.retrieve("customerSentimentsDump");
      console.log(this.customerSentimentsDump);
      this.showCustomerSentiments();
      this.retrievedCustomerSentiment = true;
      // if(this.customerSentiment != null && this.customerSentimentsDump != null) {
        
      // } else {
      //   this.showNoGraph = true;
      // }
    } else {
      if(this.customerSentimentsDump==null || this.customerSentimentsDump==undefined){
        this.sessionStorage.observe('customerSentimentsDump').subscribe((custSent1) => this.getSentimentDataDump(custSent1)); // or through observable
      }if(this.customerSentiment==null || this.customerSentiment==undefined){
        this.sessionStorage.observe('customerSentiments').subscribe((custSent) => this.getSentimentData(custSent)); // or through observable
      }

    }
  }

  loadRecommendation() {
    if(this.ticketInsightData.resRecommendations) {
      this.ticketInsightLoaded = true;
      this.recommendations = this.ticketInsightData.resRecommendations;
    } else {
      this.showNoData = true;
    }
  }

  //subscribed sentiments data 
  getSentimentDataDump(custSent1: CustomerSentiments[]) {
    this.customerSentimentsDump = custSent1;
    this.sentimentsDataLoadedInInsight = true;
    if(this.customerSentimentsDump != null && this.customerSentiment!=null) {
      this.showNoGraph = true;
      this.showCustomerSentiments();
      this.observedCustomerSentiment = true;
    } else {
      //this.showNoGraph = true;
    }
    // if(this.customerSentimentsDump != null) {
    //   this.showCustomerSentiments();
    //   this.observedCustomerSentiment = true;
    // } else {
    //   this.showNoGraph = true;
    // }
  }
    //subscribed sentiments data 
    getSentimentData(custSent: CustomerSentimentData) {
      this.customerSentiment = custSent;
      this.sentimentsDataLoadedInInsight = true;
      if(this.customerSentimentsDump != null && this.customerSentiment!=null) {
        this.showNoGraph = true;
        this.showCustomerSentiments();
        this.observedCustomerSentiment = true;

      } else {
      }
     
    }
    


  //method to show the graph of customer in left nav bar
  showCustomerSentiments() {
    this.totalFeedback = this.customerSentiment.totalFeedback;
    //this.locationId = this.sessionStorage.retrieve("locationId");
   

    for(let i=0; i<this.customerSentiment.categoryGraph.length; i++) {
      this.categoryArray[i] = this.customerSentiment.categoryGraph[i].Category;
      this.categoryCountArray[i] = this.customerSentiment.categoryGraph[i].count;
      if(this.categoryCountArray[i]>0){this.showFeedbackCategoriesGraph = true;}
      //console.log("thirdGraphArrayCategory : " +this.thirdGraphArrayCategory+ "  thirdGraphArrayCategoryCt : " +this.thirdGraphArrayCategoryCt);
    }

   
    this.horizontalBarGraphChartOptions = {
      legend: {
        display: false
      },
      scales: {
        yAxes: [{
          scaleLabel: {
          },
          ticks: {
            stepSize: 1,
            suggestedMax: 1,
            beginAtZero: true
          },
          gridLines: {
            display: false
          }
        }],
        xAxes: [{
          scaleLabel: {
             display: true,
             labelString: "Count of DSAT Categories --->",
             fontColor: '#209e9e',
             fontSize: 12
          },
          ticks: {
            beginAtZero: true,
            stepSize: 1,
            suggestedMax: 1,
          },
          gridLines: {
            display: false
          }
        }]
      }
    }
    this.horizontalBarGraphData = {
      labels: this.categoryArray,
      fill: true,
      backgroundColor: [
        "#ed1c24",
        "#ffc90e",
        "#009b9b",
        "#005b5b",
        "#003333",
        "#003333"
      ],
      datasets: [
        {
          label: 'Trends: DSAT Feedback',
          fontColor: '#004d4d',
          fontSize: 20,
          backgroundColor: '#4bc0c0',
          borderColor: '#4bc0c0',
          data: this.categoryCountArray
        },
      ]
    }

    
    //this.avgRating = this.totalRating / this.customerSentiment.length;
    
    this.pieGraphChartOptions = {
      legend: {
        position: 'top',
        display: true
      },
      scales: {
       
      }
    }
    this.pieData = {
      type:'pie',
      labels: ['CSAT','DSAT'],
      datasets: [
        {
          data: [this.customerSentiment.totalCsatCount, this.customerSentiment.totalDsatCount],
          
          backgroundColor: [
            "#018282",
            "#e8052b"
          ],
          hoverBackgroundColor: [
            "#018282",
            "#e8052b"
          ]
        }
      ]    
    };
    this.avgRating = Number(this.customerSentiment.averageRating);
      console.log(this.customerSentimentsDump.length)
      if(this.avgRating < 3 && this.customerSentimentsDump.length!=0) {
        this.showAvgAlert = true;
      }
      this.canvasWidth = 250;
      this.needleValue = this.avgRating*20;
      this.centralLabel = '';
      this.bottomLabel = this.avgRating.toFixed(1);
      this.options = {
        hasNeedle: true,
        needleColor: '#00509a',
        needleUpdateSpeed: 1000,
        arcColors: ['#cf0804', '#f57f11','#f5da11','#059c9c', '#015252'],
        arcDelimiters: [1*20,2*20,3*20,4*20],
        rangeLabel: ['0','5'],
        needleStartValue: this.avgRating*20,
        canvasWidth: 20,
      }
      this.showGaugeChart = true;
      if(this.customerSentiment.totalFeedback != 0) {
        this.showTotalFeedbackGraph = true;
        this.showAvgRatingGraph = true;
        this.sentimentsDataLoadedInInsight = true;
      }
    
  }

  //method to open resolution recommendation on click of plus icon for more details
  async getRecommendationDetails(rec: ResolutionRecommendations) {    
      let loggingEventData = { ...this.loggingEvent};
      loggingEventData.componentName = this.componentName;
      loggingEventData.eventTriggered = "Resolution Recommendation (+) icon clicked of Incident : "+ rec.incident;
      this.restApiService.reportLoggingEvent(loggingEventData).subscribe((response: boolean)=>{});
      this.recommendationDetails = rec;
      let feedback = this.feedbackMap.get(rec.incident);
      if(feedback) {
        this.resFeedback = feedback;
      } else {
        let feedbackObj = new ResolutionFeedbackModel();
        feedbackObj.recommendationDetails = rec;
        feedbackObj.user = this.loginUserName;
        feedbackObj.problemDescription = this.shortDescription;
        feedbackObj.isHelpful = false;
        feedbackObj.isIncomplete = false;
        feedbackObj.isIrrelevant = false;
        feedbackObj.isOutdated = false;
        this.feedbackMap.set(rec.incident, feedbackObj);
        this.resFeedback = feedbackObj;
      }
  }

  //method for back button after clearing the session variables.
  routeTo() {
    this.sessionStorage.clear();
    this.sessionStorage.store("username", this.loginUserName);
    this.router.navigate(['/main']);
  }

  //method to set the recommendation feedback smileys
  feedback(userFeedback: string, incident: string) {
    let feedbackObj = this.feedbackMap.get(incident);
    //this.resetFeedbackFlag(feedbackObj)
    if(userFeedback == "Helpful") {
      feedbackObj.isHelpful = true;
    }
    if(userFeedback == "Outdated") {
      feedbackObj.isOutdated = true;
    }
    if(userFeedback == "Incomplete") {
      feedbackObj.isIncomplete = true;
    }
    if(userFeedback == "Irrelevant"){
      feedbackObj.isIrrelevant = true;
    }
    this.feedbackMap.set(incident, feedbackObj);
  }

  //methos to reset the smiley of resolution feedback
  resetFeedbackFlag(feedbackObj: ResolutionFeedbackModel){
    feedbackObj.isOutdated = false;
    feedbackObj.isHelpful = false;
    feedbackObj.isIrrelevant = false;
    feedbackObj.isIncomplete = false;
    return feedbackObj;
  }

  //method to submit feedback
  submitFeedback(incident: string) {
    let feedbackObj = this.feedbackMap.get(incident)
    if(!feedbackObj.isIrrelevant && !feedbackObj.isHelpful && !feedbackObj.isIncomplete
      && !feedbackObj.isOutdated) {
        console.log("no feedback selected")
        showModal('unsuccess-Modal');
        return;
    } else {
      this.restApiService.submitUserRecommendationFeedback(feedbackObj).subscribe((response: boolean) => {
        this.feedbackResponse = response;
      });
      closeModal('recommendation-Modal');
      showModal('success-Modal');
    }    
  }
  
  //method for copy clipboard
  copyInputMessage(inputElement) {  
    let range = document.createRange();
    range.selectNode(inputElement);
    window.getSelection().removeAllRanges(); // clear current selection
    window.getSelection().addRange(range); // to select text
    document.execCommand("copy");
    window.getSelection().removeAllRanges();
    //event logging
    let loggingEventData = { ...this.loggingEvent};
    loggingEventData.componentName = this.componentName;
    loggingEventData.eventTriggered = "Resolution Notes copied to clipboard";
    this.restApiService.reportLoggingEvent(loggingEventData).subscribe((response: boolean)=>{});
  } 

  textMessageFunc(msgText) {  
    this.textMessage = msgText+" Copied to Clipboard";  
    this.msgHideAndShow = true;  
    setTimeout(() => {  
    this.textMessage = "";  
    this.msgHideAndShow = false;  
     }, 1000);  
   } 

  //method to check if user has accessed the page thru valid path
   validateEntry(){
    if(this.loginUserName == "" ||this.loginUserName == null || this.loginUserName == undefined) {
      this.sessionStorage.clear();
      this.router.navigate(['/login']);
    }
  }

    // event logging for opening sentiments table
    customerSentimentOpen() {
      let loggingEventData = { ...this.loggingEvent};
      loggingEventData.componentName = this.componentName;
      loggingEventData.eventTriggered = "Sentiment table opened.";
      this.restApiService.reportLoggingEvent(loggingEventData).subscribe((response: boolean)=>{});
    }
}
